<template>
  <div class="modal" @click="$emit('closeModal')">
    <div class="modal__window" @click.stop>
      <h2 class="modal__title">
        {{ edit ? "Elsatmani tahrirlash" : "Eslatma qo’shish" }}
      </h2>
      <div class="modal__inputs">
        <label class="modal__label">
          <span>Title</span>
          <input
            type="text"
            class="modal__inp"
            placeholder="Tile"
            v-model="title"
          />
        </label>
        <label class="modal__label">
          <span>Content</span>
          <input
            type="text"
            class="modal__inp"
            placeholder="Content"
            v-model="text"
          />
        </label>
      </div>
      <div class="modal__buttons">
        <button class="modal__btn del" @click="$emit('closeModal')">
          Bekor qilish
        </button>
        <button v-if="edit" class="modal__btn edit" @click="changeNote">
          o'zgartirish
        </button>
        <button v-else class="modal__btn edit" @click="addNote">
          Qo’shish
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      title: null,
      text: null,
    };
  },
  methods: {
    addNote() {
      if (this.title != null && this.text != null) {
        let newNote = {
          title: this.title,
          text: this.text,
        };
        this.$emit("addNote", newNote);
      }
    },
    changeNote() {
      if(this.title != '' && this.text != ''){
        const newNote = {
            id:this.editedNote.id,
            title:this.title,
            text:this.text,
            date: new Date().toLocaleDateString()
        }
        this.$emit('changeNote', newNote)
      }
    },
  },
  props: {
    edit: Boolean,
    editedNote: Object,
    lang:String
  },
  inject:['words'],
  mounted() {
    if (this.edit) {
      this.title = this.editedNote.title;
      this.text = this.editedNote.text;
    }
  },
};
</script>

