<template>
  <Navbar @changeLang="changeLang" :lang="lang" @searchValue="search = $event"/>
  <Notes :lang="lang" :notes="filterNotes" @delNote="delNote" @editNote="editNote"/>
  <AddButton @click="modal = true" />
  
  <Modal 
  v-if="modal" \
  :lang="lang"
  @closeModal="closeModal" 
  @addNote="addNote"
  :edit="edit"
  :editedNote="editedNote"
  @changeNote="changeNote"
  />
</template>
<script>
import Navbar from "@/components/Navbar.vue";
import Notes from "@/components/Notes.vue";
import AddButton from "@/components/AddButton.vue";
import Modal from "@/components/Modal.vue";
import Lang from "@/lang.js";
let id = 1;
export default {
  
  components: {
    Navbar,
    Notes,
    AddButton,
    Modal,
  },
  data() {
    return {
      search:null,
      modal: false,
      notes: [
        {
          id: id++,
          title: "Vue",
          text: "Vue bu eng kichik hajmli freamework",
          date: "07.05.2014",
        },
        {
          id: id++,
          title: "React",
          text: "React keng tarqalgan freamework",
          date: "07.05.2012",
        },
        {
          id: id++,
          title: "Angular",
          text: "Angular bu eng katta hajmli freamework",
          date: "07.05.2009",
        },
      ],
      edit:false,
      editedNote:{},
      lang: 'ru',
      langwords:{}
    };
  },
  methods:{
    changeLang(value){
      this.lang = localStorage.lang = value
    },
    changeNote(newNote){
      this.notes.forEach(note => {
        if (note.id == newNote.id) {
          note.title = newNote.title,
          note.text = newNote.text,
          note.date = newNote.date
        }
      })
      this.closeModal()
    },
    editNote(id){
      this.edit = this.modal = true
      this.editedNote = this.notes.find(note => note.id == id)
    },
    delNote(index){
      this.notes.splice(index, 1)
    },
    closeModal(){
     this.edit = this.modal = false
    },
    addNote(note){
      this.notes.push({
        id:this.notes.length + 1 ,
        title:note.title,
        text:note.text,
        date: new Date().toLocaleDateString()
      })
        this.closeModal()
    }
  },
  computed:{
    filterNotes(){
      return this.search ? this.notes.filter(note => note.title.toLowerCase().includes(this.search.toLowerCase())) : this.notes
    }
  },
  watch:{
    notes:{
      handler(newNotes){
        localStorage.list = JSON.stringify(newNotes)
      },
      deep:true
    }
  },
  created(){
    let localUsers = localStorage.list
    if (localUsers) {
      this.notes = JSON.parse(localUsers)
    }
    this.lang = localStorage.lang = localStorage.lang || "ru"
    localStorage.words = JSON.stringify(Lang)
  },
  provide(){
    return{
      words: localStorage.words ? JSON.parse(localStorage.words) : Lang
    }
  }
  
};
</script>

