<template>
  <div class="container">
    <div class="notes">
      <div class="notes__top">
        <h2 class="notes__title">{{words.infobar[lang]}}</h2>
        <button class="notes__btn" @click="grid = !grid">
          <img src="@/assets/images/grid.svg" alt="" v-if="grid" />
          <img src="@/assets/images/list.svg" alt="" v-else />
          <span>{{ grid ? `Setka` : `Ro'yxat` }}</span>
        </button>
      </div>
      <div class="notes__list" :class="{ active: grid }">
        <NoteItem 
        :lang="lang"
        v-for="note,i in notes" 
        :key="note.id" 
        :note="note" 
        @delNote="$emit('delNote', i)"
        @editNote="$emit('editNote', note.id)"
        />
      </div>
    </div>
  </div>
</template>

<script>
import NoteItem from "@/components/NoteItem.vue";
export default {
  data() {
    return {
      grid: false,
    };
  },
  components: {
    NoteItem,
  },
  props: {
    notes: Array,
    lang:String
  },
  inject:['words']
};
</script>

